import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation, useNavigate } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import {
  useAdmissionOngoingApplicationConfirm,
  useAdmissionOngoingApplicationConfirmReview,
} from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AdmissionConfirmCard from "./AdmissionConfirmCard";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";

const AdmissionMultipleConfirm = ({ applicationDetail, aid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [applicantId, setApplicantId] = useState([]);
  const [disabled, setDisabled] = useState(false);

  const [admissionOngoingApplicationConfirmReview] =
    useAdmissionOngoingApplicationConfirmReview();
  const {
    admissionOngoingApplicationConfirm,
    admissionOngoingApplicationConfirmLoading,
    admissionOngoingApplicationConfirmRefetch,
  } = useAdmissionOngoingApplicationConfirm({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationConfirm?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationConfirm?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationConfirm?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    admissionOngoingApplicationConfirmRefetch();
  }, []);

  const onNavigateBack = () => {
    navigate(-1);
  };

  const onReview = () => {
    if (applicantId?.length > 0 && getQuery.state?.appId) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationConfirmReview({
        appId: getQuery.state?.appId,
        review: {
          student_arr: applicantId,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onSelect = (e, val) => {
    e.stopPropagation();
    if (applicantId?.includes(val))
      setApplicantId((prev) => prev?.filter((id) => id !== val));
    else setApplicantId((prev) => [...prev, val]);
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
        >
          <input
            type="text"
            placeholder={t("search_applicants")}
            onChange={onSearchEvent}
            style={{
              width: "100%",
            }}
          />
        </OneOngoingAdmissionWrapper>
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <AddButtonWrapper>
            <AdmissionButton
              label="review_application"
              onAction={onReview}
              customStyle={{
                padding: "0.5rem 1rem",
              }}
            />
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onNavigateBack}
              style={{
                cursor: "pointer",
              }}
            />
          </AddButtonWrapper>
          {admissionOngoingApplicationConfirm?.list?.map((applicant, index) =>
            admissionOngoingApplicationConfirm?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <AdmissionConfirmCard
                  applicant={applicant}
                  instituteId={instituteId}
                  applicantId={applicantId}
                  onSelect={onSelect}
                  isMultiple
                />
              </div>
            ) : (
              <AdmissionConfirmCard
                applicant={applicant}
                key={applicant?._id}
                instituteId={instituteId}
                applicantId={applicantId}
                onSelect={onSelect}
                isMultiple
              />
            )
          )}
          {admissionOngoingApplicationConfirmLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionOngoingApplicationConfirm?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants fuond related this search."
              />
            )
          ) : (
            admissionOngoingApplicationConfirm?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionMultipleConfirm;
