import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../OngoingAdmission/OngoingAdmission.module.css";
const SelectDivisionsCard = ({ onSelect, cls, isView, selectedCls }) => {
  const onSelectData = () => {
    onSelect(cls);
  };
  return (
    <div
      className={
        selectedCls === cls?._id
          ? `${style.admission_confirm_class_card} ${style.admission_confirm_class_card_active}`
          : style.admission_confirm_class_card
      }
      onClick={onSelectData}
    >
      <img
        src={`${assestsUserFinanceUrl}/class.svg`}
        alt="Class Avatar"
        loading="lazy"
      />
      <div
        className={style.admission_collect_docs}
        style={{
          alignItems: "flex-start",
          marginTop: "0",
        }}
      >
        <h6>{`${cls?.className ?? ""} - ${cls?.classTitle ?? ""}`}</h6>
        {isView === "CLASS_TEACHER" ? null : (
          <div className={style.admission_confirm_class_count}>
            <p>Boys : {cls?.boyCount}</p>
            <p>Girls : {cls?.girlCount}</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default SelectDivisionsCard;
