import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import AdmissionManagerProfile from "./AdmissionManagerProfile";
import { useAdmissionDashboardMaster } from "../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import PaymentVerification from "./PaymentVerification/PaymentVerification";
import OngoingAdmission from "./OngoingAdmission/OngoingAdmission";
import OneOngoingAdmissionConfig from "./OneOngoingAdmission/OneOngoingAdmissionConfig";
const FinanceAllStudent = lazy(() =>
  import("../FinanceManager/FinanceAllStudent/FinanceAllStudent")
);
const StudentMessageHistory = lazy(() =>
  import(
    "../FinanceManager/FinanceAllStudent/StudentMessageHistory/StudentMessageHistory"
  )
);
const ExcessFees = lazy(() => import("./ExcessFees/ExcessFees"));
const RequiredDocument = lazy(() =>
  import("./RequiredDocument/RequiredDocument")
);
const AdmissionExport = lazy(() => import("./AdmissionExport/AdmissionExport"));
const AdmissionModerator = lazy(() => import("./Moderator/AdmissionModerator"));

const AdmissionManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { admissionDashboardMaster } = useAdmissionDashboardMaster({
    data: {
      aid: getQuery.state?.aid,
      mid: "",
    },
    skip: !getQuery.state?.aid,
  });

  // console.info("sdjbhfhjsbfnsdf", admissionDashboardMaster);
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ||
          ["fund"]?.includes(searchParams.get("type")) ? (
            <AdmissionManagerProfile
              admissionData={{
                onlineFee: admissionDashboardMaster?.onlineFee,
                offlineFee: admissionDashboardMaster?.offlineFee,
                collected_fee: admissionDashboardMaster?.collected_fee,
                remainingFeeCount: admissionDashboardMaster?.remainingFeeCount,
                exemptAmount: admissionDashboardMaster?.exemptAmount,
                requested_status: admissionDashboardMaster?.requested_status,
              }}
              aid={admissionDashboardMaster?._id}
              tabManage={admissionDashboardMaster?.tab_manage}
            />
          ) : searchParams.get("type") === "students" ? (
            <FinanceAllStudent
              instituteId={admissionDashboardMaster?.institute?._id}
              messageId={admissionDashboardMaster?._id}
              headId={admissionDashboardMaster?.admissionAdminHead?._id}
              studentAlias={
                admissionDashboardMaster?.institute?.alias_pronounciation
                  ?.student_alias
              }
              accessAs="ADMISSION_ADMIN"
              subUrl="admission"
            />
          ) : searchParams.get("type") ===
            "moderator" ? null : searchParams.get("type") === "message" ? ( // /> //   instituteId={admissionDashboardMaster?.finance?.institute?._id} //   fid={getQuery.state?.fid} // <FinanceModerator
            <StudentMessageHistory
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "excess" ? (
            <ExcessFees
              instituteId={admissionDashboardMaster?.institute?._id}
              totalRefund={admissionDashboardMaster?.refundCount}
              aid={admissionDashboardMaster?._id}
            />
          ) : searchParams.get("type") === "document" ? (
            <RequiredDocument aid={admissionDashboardMaster?._id} />
          ) : searchParams.get("type") === "export" ? (
            <AdmissionExport
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "moderator" ? (
            <AdmissionModerator
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "verification" ? (
            <PaymentVerification
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "ongoing" ? (
            <OngoingAdmission
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
              fid={admissionDashboardMaster?.institute?.financeDepart?.[0]}
            />
          ) : searchParams.get("type") === "ongoing-detail" ? (
            <OneOngoingAdmissionConfig
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default AdmissionManager;
