import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import style from "../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useAdmissionPaymentVerifyList } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import PaymentVerificationCard from "./PaymentVerificationCard";

const PaymentVerificationRequest = ({ aid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const { admissionPaymentVerifyList, admissionPaymentVerifyListLoading } =
    useAdmissionPaymentVerifyList({
      data: {
        aid: aid,
        page: page,
        limit: 10,
        search: search,
        filter: "",
        over_filter: "",
      },
      skip: !aid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionPaymentVerifyList?.list?.length) {
      let obj = nextPage(admissionPaymentVerifyList?.list?.length);
      setState(() => obj);
    }
  }, [admissionPaymentVerifyList?.list]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>

      {admissionPaymentVerifyList?.list?.map((history, index) =>
        admissionPaymentVerifyList?.list?.length === index + 1 ? (
          <div key={history?._id} ref={ref}>
            <PaymentVerificationCard history={history} />
          </div>
        ) : (
          <PaymentVerificationCard history={history} key={history?._id} />
        )
      )}
      {admissionPaymentVerifyListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        !admissionPaymentVerifyList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No request fuond related this search."
          />
        )
      ) : (
        !admissionPaymentVerifyList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No request found."
          />
        )
      )}
    </>
  );
};

export default PaymentVerificationRequest;
