import { useNavigate, useParams } from "react-router-dom";
import style from "../OneOngoingAdmission.module.css";
import { useCallback, useState } from "react";
import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import AdmissionApplicationFeeReceipt from "../../../TransactionReceipt/AdmissionApplicationFeeReceipt";
import { useAdmissionOngoingApplicationConfirmReview } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const AdmissionConfirmCard = ({
  instituteId,
  applicant,
  appId,
  onRefetch,
  isMultiple,
  onSelect,
  applicantId,
}) => {
  const params = useParams();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [admissionOngoingApplicationConfirmReview] =
    useAdmissionOngoingApplicationConfirmReview();

  const onViewApplicantProfile = useCallback(() => {
    navigate(`/q/${params.username}/student/profile`, {
      state: {
        sid: applicant?.student?._id,
        viewAs: "ADMISSION_ADMIN",
        instituteId: instituteId,
      },
    });
  }, [instituteId, applicant?.student?._id]);

  const onViewApplication = (e) => {
    e.stopPropagation();
  };
  const onReview = (e) => {
    e.stopPropagation();
    if (applicant?.student?._id && appId) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationConfirmReview({
        appId: appId,
        review: {
          student_arr: [
            {
              sid: applicant?.student?._id,
              cid: applicant?._id,
            },
          ],
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <div
        className={style.applicant_card_container}
        onClick={onViewApplicantProfile}
        title="View Profile"
      >
        {isMultiple ? (
          <img
            className={style.department_checkbox}
            src={
              applicantId?.includes(applicant?.student?._id)
                ? `${assestsUserAdmissionUrl}/check.svg`
                : `${assestsUserAdmissionUrl}/uncheck.svg`
            }
            alt="check icon"
          />
        ) : null}
        <div className={style.applicant_card_inner}>
          <img
            alt="Student Request Avatar"
            src={
              applicant?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${applicant?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
          />
          <div className={style.applicant_card_inner_info}>
            <h6>{`${applicant?.student?.studentFirstName ?? ""} ${
              applicant?.student?.studentMiddleName
                ? applicant?.student?.studentMiddleName
                : ""
            } ${applicant?.student?.studentLastName ?? ""}`}</h6>
          </div>
        </div>

        <div className={style.applicant_card_contact}>
          <div className={style.applicant_card_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/gender.svg`}
              alt="email icon"
            />
            <p>{applicant?.student?.studentGender}</p>
          </div>
          {(applicant?.student?.studentPhoneNumber ||
            applicant?.student?.user?.userPhoneNumber ||
            applicant?.student?.user?.userEmail ||
            applicant?.student?.studentParentsPhoneNumber) && (
            <div className={style.applicant_card_contact_inner}>
              <img
                src={`${assestsUserAdmissionUrl}/phone.svg`}
                alt="phone icon"
              />
              <p>
                {applicant?.student?.user?.userPhoneNumber
                  ? applicant?.student?.user?.userPhoneNumber
                  : applicant?.student?.user?.userEmail
                  ? applicant?.student?.user?.userEmail
                  : applicant?.student?.studentPhoneNumber
                  ? applicant?.student?.studentPhoneNumber
                  : applicant?.student?.studentParentsPhoneNumber}
              </p>
            </div>
          )}
        </div>
        <div
          className={style.applicant_card_reciept}
          onClick={onViewApplication}
        >
          <a
            href={`${pdfCdnUrl}/${applicant?.student?.application_print?.[0]?.value}`}
            target="_blank"
            rel="noopener noreferrer"
            style={{
              textDecoration: "none",
            }}
          >
            <img
              src={`${assestsUserAdmissionUrl}/download.svg`}
              alt="receipt icon"
              title="View Application Form"
            />
          </a>
        </div>
        <div className={style.applicant_card_reciept}>
          {applicant?.student?.fee_receipt?.map((rid, index) =>
            index > 2 ? (
              ""
            ) : (
              <AdmissionApplicationFeeReceipt rid={rid} key={rid} />
            )
          )}
        </div>
        {isMultiple ? (
          <AddButtonWrapper>
            <AdmissionButton
              label="select"
              onAction={(e) => onSelect(e, applicant?.student?._id)}
              customStyle={{
                padding: "0.4rem 0.7rem",
              }}
            />
          </AddButtonWrapper>
        ) : (
          <AddButtonWrapper>
            <AdmissionButton
              label="review_application"
              onAction={onReview}
              customStyle={{
                padding: "0.4rem 0.7rem",
              }}
            />
          </AddButtonWrapper>
        )}
      </div>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionConfirmCard;
