import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import { useAdmissionOngoingApplicationReview } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import AdmissionReviewCard from "./AdmissionReviewCard";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import AdmissionReviewConfig from "./AdmissionReviewConfig";
const AdmissionMultipleReview = ({ applicationDetail, aid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [applicantId, setApplicantId] = useState([]);
  const [step, setStep] = useState("");
  const {
    admissionOngoingApplicationReview,
    admissionOngoingApplicationReviewLoading,
    admissionOngoingApplicationReviewRefetch,
  } = useAdmissionOngoingApplicationReview({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationReview?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationReview?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationReview?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    admissionOngoingApplicationReviewRefetch();
  }, []);

  const onNavigateBack = () => {
    navigate(-1);
  };

  const onSelect = (e, val) => {
    e.stopPropagation();
    if (applicantId?.includes(val))
      setApplicantId((prev) => prev?.filter((id) => id !== val));
    else setApplicantId((prev) => [...prev, val]);
  };
  const onClose = () => {
    setStep("");
  };

  const onAllottment = () => {
    setStep(1);
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
        >
          <input
            type="text"
            placeholder={t("search_applicants")}
            onChange={onSearchEvent}
            style={{
              width: "100%",
            }}
          />
        </OneOngoingAdmissionWrapper>
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <AddButtonWrapper>
            <AdmissionButton
              label="allot_class"
              onAction={onAllottment}
              customStyle={{
                padding: "0.5rem 1rem",
              }}
            />
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onNavigateBack}
              style={{
                cursor: "pointer",
              }}
            />
          </AddButtonWrapper>
          {admissionOngoingApplicationReview?.list?.map((applicant, index) =>
            admissionOngoingApplicationReview?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <AdmissionReviewCard
                  applicant={applicant}
                  instituteId={instituteId}
                  applicantId={applicantId}
                  onSelect={onSelect}
                  isMultiple
                />
              </div>
            ) : (
              <AdmissionReviewCard
                applicant={applicant}
                key={applicant?._id}
                instituteId={instituteId}
                applicantId={applicantId}
                onSelect={onSelect}
                isMultiple
                onAllttment={onAllottment}
              />
            )
          )}
          {admissionOngoingApplicationReviewLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionOngoingApplicationReview?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants fuond related this search."
              />
            )
          ) : (
            admissionOngoingApplicationReview?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {step === 1 ? (
        <AdmissionReviewConfig
          appId={getQuery.state?.appId}
          applicant={applicantId}
          onClose={onClose}
          onRefetch={onRefetch}
          isMultiple
        />
      ) : null}
    </>
  );
};

export default AdmissionMultipleReview;
