import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import AdmissionAllottmentCard from "./AdmissionAllottmentCard";
import { useAdmissionOngoingApplicationAllottment } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const AdmissionAllottment = ({ applicationDetail, instituteId, aid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOngoingApplicationAllottment,
    admissionOngoingApplicationAllottmentLoading,
  } = useAdmissionOngoingApplicationAllottment({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationAllottment?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationAllottment?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationAllottment?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper applicationDetail={applicationDetail}>
          <input
            type="text"
            placeholder={t("search_allotted_student")}
            onChange={onSearchEvent}
            style={{
              width: "100%",
            }}
          />
        </OneOngoingAdmissionWrapper>
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {admissionOngoingApplicationAllottment?.list?.map(
            (applicant, index) =>
              admissionOngoingApplicationAllottment?.list?.length ===
              index + 1 ? (
                <div key={applicant?._id} ref={ref}>
                  <AdmissionAllottmentCard applicant={applicant} />
                </div>
              ) : (
                <AdmissionAllottmentCard
                  applicant={applicant}
                  key={applicant?._id}
                />
              )
          )}
          {admissionOngoingApplicationAllottmentLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionOngoingApplicationAllottment?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission allottment fuond related this search."
              />
            )
          ) : (
            admissionOngoingApplicationAllottment?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission allottment list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default AdmissionAllottment;
