import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useAdmissionOngoingApplicationRequest } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { useLocation } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import AdmissionApplicationCard from "./AdmissionApplicationCard";

const AdmissionApplication = ({ applicationDetail, aid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOngoingApplicationRequest,
    admissionOngoingApplicationRequestLoading,
    admissionOngoingApplicationRequestRefetch,
  } = useAdmissionOngoingApplicationRequest({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationRequest?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationRequest?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationRequest?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    admissionOngoingApplicationRequestRefetch();
  }, []);
  // console.info("applicationDetail", applicationDetail);
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
        >
          <input
            type="text"
            placeholder={t("search_applicants")}
            onChange={onSearchEvent}
            style={{
              width: "100%",
            }}
          />
        </OneOngoingAdmissionWrapper>
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {admissionOngoingApplicationRequest?.list?.map((applicant, index) =>
            admissionOngoingApplicationRequest?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <AdmissionApplicationCard
                  applicant={applicant}
                  instituteId={instituteId}
                  did={applicationDetail?.applicationDepartment?._id}
                  bid={applicationDetail?.applicationBatch?._id}
                  cmid={applicationDetail?.applicationMaster}
                  onRefetch={onRefetch}
                  appId={getQuery.state?.appId}
                />
              </div>
            ) : (
              <AdmissionApplicationCard
                key={applicant?._id}
                applicant={applicant}
                instituteId={instituteId}
                did={applicationDetail?.applicationDepartment?._id}
                bid={applicationDetail?.applicationBatch?._id}
                cmid={applicationDetail?.applicationMaster}
                onRefetch={onRefetch}
                appId={getQuery.state?.appId}
              />
            )
          )}
          {admissionOngoingApplicationRequestLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionOngoingApplicationRequest?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission requests fuond related this search."
              />
            )
          ) : (
            admissionOngoingApplicationRequest?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission request list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default AdmissionApplication;
