import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useAdmissionCashToFinance } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useState } from "react";
const AdmissionFund = ({ admissionData, aid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [admissionCashToFinance] = useAdmissionCashToFinance();
  const onCashSubmit = () => {
    if (
      aid &&
      admissionData?.collected_fee > 0 &&
      admissionData?.requested_status === "Pending"
    ) {
      setDisabled((pre) => !pre);
      admissionCashToFinance({
        aid: aid,
        submitAmount: { amount: admissionData?.collected_fee },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("admission_fees")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        <div className={style.fm_fund_total}>
          <img src={`${assestsUserFinanceUrl}/rupee.svg`} alt="" />
          <div className={style.fm_fund_total_inner}>
            <h6>
              {t("rs_only")}{" "}
              {getAdd(admissionData?.onlineFee, admissionData?.offlineFee)}
            </h6>
            <p>{t("total_admission_fees")}</p>
          </div>
        </div>
        <div className={style.fm_fund_card}>
          <p>{t("online_fees")}:</p>
          <p>
            {t("rs_only")} {admissionData?.onlineFee?.toFixed(2) ?? 0}
          </p>
        </div>
        <div className={style.fm_fund_card}>
          <p>{t("offline_fees")}:</p>
          <p>
            {t("rs_only")} {admissionData?.offlineFee?.toFixed(2) ?? 0}
          </p>
        </div>

        <div className={style.fm_fund_card}>
          <p>{t("cash_with_me")}:</p>
          <p>
            {t("rs_only")} {admissionData?.collected_fee?.toFixed(2) ?? 0}
          </p>
        </div>
        <div className={style.fm_fund_card}>
          <p>{t("total_pending_fees")}:</p>
          <p>
            {t("rs_only")} {admissionData?.remainingFeeCount?.toFixed(2) ?? 0}
          </p>
        </div>
        <div className={style.fm_fund_card}>
          <p>{t("total_concession_fees")}:</p>
          <p>
            {t("rs_only")} {admissionData?.exemptAmount?.toFixed(2) ?? 0}
          </p>
        </div>
        <CreateButton
          label="submit_cash_to_finance_manager"
          onAction={onCashSubmit}
        />
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionFund;

const getAdd = (a, b) => {
  if (a && b) {
    return +a + +b;
  } else {
    return 0;
  }
};
